// src/components/StadiumScanScreen.js
import React, { useEffect, useRef, useState } from 'react'
import { Html5Qrcode, Html5QrcodeScannerState } from 'html5-qrcode'

export default function StadiumScanScreen({ onScanSuccess }) {
  const [scanned, setScanned] = useState(false)
  const scannerRef = useRef(null)

  useEffect(() => {
    const regionId = 'qr-reader'
    const scanner = new Html5Qrcode(regionId)
    scannerRef.current = scanner

    scanner.start(
      { facingMode: 'environment' },
      { fps:10, qrbox:{width:250,height:250} },
      () => {
        if(!scanned) {
          setScanned(true)
          if(scanner.getState()===Html5QrcodeScannerState.SCANNING){
            scanner.stop().catch(()=>{})
          }
        }
      },
      ()=>{}
    ).catch(err=>console.error(err))

    return () => {
      if(scannerRef.current?.getState()===Html5QrcodeScannerState.SCANNING){
        scannerRef.current.stop().catch(()=>{})
      }
    }
  },[scanned])

  useEffect(()=>{
    if(scanned){
      const t=setTimeout(onScanSuccess,2000)
      return ()=>clearTimeout(t)
    }
  },[scanned,onScanSuccess])

  return (
    <div className="relative w-full max-w-[375px] h-full flex flex-col items-center justify-center p-4 bg-black">
      <div className="absolute inset-0 bg-black opacity-80"/>
      <div className="relative z-20 text-center">
        <h2 className="text-white text-xl mb-4">구장 내 QR코드를 인식해주세요!</h2>
        <div id="qr-reader" className="w-64 h-64 border-2 border-white overflow-hidden"/>
        <p className={`mt-4 text-lg ${scanned?'text-[#C7FF00]':'text-white'}`}>
          {scanned?'스캔 완료!':'스캔 중...'}
        </p>
      </div>
    </div>
  )
}
