// src/components/CountdownScreen.js
import React from 'react';

export default function CountdownScreen({ count }) {
  return (
    <div className="flex items-center justify-center h-full">
      {/* 
        숫자마다 key를 count로 달아서 
        값이 바뀔 때마다 span이 새로 마운트되고 
        animate-fade-in 애니메이션이 재실행됩니다. 
      */}
      <span
        key={count}
        className="text-white text-6xl font-bold animate-fade-in"
      >
        {count}
      </span>
    </div>
  );
}
