// src/components/ModeSelector.js
import React from 'react';
import { useMode } from '../ModeContext';
import Icon from '../assets/icon.png';

export default function ModeSelector({ onNext }) {
  const { setMode } = useMode();

  const btn = (m,label) => (
    <button
      onClick={() => { setMode(m); onNext(m); }}
      className="w-full py-3 bg-[#C7FF00] rounded-full text-black text-lg font-extrabold hover:opacity-90 active:scale-95 transition"
    >{label}</button>
  );

  return (
    <div className="flex flex-col items-center justify-center h-full w-full space-y-6"
         style={{ fontFamily:'Pretendard, sans-serif' }}>
      <p className="text-white text-xl">가장 빠른 사람은 누구?!</p>
      <h1 className="flex items-center text-white text-4xl font-bold">
        축구공 연타 대결
        <img src={Icon} alt="" className="w-12 h-12 ml-2"/>
      </h1>
      <p className="text-white text-xl mb-6">모드를 선택해주세요!</p>
      <div className="w-3/4 max-w-xs space-y-4">
        {btn('personal','개인전')}
        {btn('team','팀전')}
        {btn('stadium','구장전')}
      </div>
    </div>
  );
}
