// src/components/GameScreen.js
import React, { useState, useRef } from 'react';
import Ball from '../assets/soccerball.png';

export default function GameScreen({ onClick, onFinish }) {
  const containerRef = useRef(null);
  const [bouncing, setBouncing] = useState(false);
  const [particles, setParticles] = useState([]);
  const [canClick, setCanClick] = useState(true);

  // 공을 누를 때마다 호출
  const handlePointerDown = (e) => {
    e.preventDefault();
    if (!canClick) return;

    setCanClick(false);
    onClick();
    if (navigator.vibrate) navigator.vibrate(30);

    // 바운스 애니메이션 재실행
    setBouncing(false);
    requestAnimationFrame(() => setBouncing(true));

    // 파티클 생성 로직
    if (containerRef.current) {
      const rect = containerRef.current.getBoundingClientRect();
      const clientX = e.touches ? e.touches[0].clientX : e.clientX;
      const clientY = e.touches ? e.touches[0].clientY : e.clientY;
      const x = clientX - rect.left;
      const y = clientY - rect.top;

      const newParticles = Array.from({ length: 8 }).map(() => {
        const angle = Math.random() * 2 * Math.PI;
        const dist = 20 + Math.random() * 20;
        return {
          id: Math.random().toString(36).substring(2, 9),
          x,
          y,
          dx: `${Math.cos(angle) * dist}px`,
          dy: `${Math.sin(angle) * dist}px`,
        };
      });
      setParticles((prev) => [...prev, ...newParticles]);
    }

    // 클릭 잠금 해제 (100ms 후) — 이 동안에도 opacity-50는 적용되지 않습니다.
    setTimeout(() => setCanClick(true), 100);
  };

  // 바운스 애니메이션 끝나면 bouncing 상태 초기화
  const handleBounceEnd = () => {
    setBouncing(false);
  };

  // 파티클 애니메이션 끝나면 해당 파티클 제거
  const handleParticleEnd = (id) => {
    setParticles((prev) => prev.filter((p) => p.id !== id));
  };

  return (
    <div
      ref={containerRef}
      className="relative flex flex-col items-center justify-center h-full space-y-8 px-4 bg-transparent"
    >
      {/* Progress Bar (5초 애니메이션이 끝나면 onFinish 호출) */}
      <div className="w-full max-w-[375px]">
        <div className="bg-gray-300 rounded-full h-5 overflow-hidden">
          <div
            className="h-full bg-gradient-to-r from-[#B3FF00] to-[#FF91CA] animate-progress"
            onAnimationEnd={onFinish}
            style={{ transformOrigin: 'left' }}
          />
        </div>
      </div>

      {/* 축구공 버튼 (opacity-50 삭제, hence no “dissolve” effect) */}
      <button
        onPointerDown={handlePointerDown}
        onAnimationEnd={handleBounceEnd}
        disabled={!canClick}
        className={`${
          canClick ? "cursor-pointer" : "cursor-not-allowed"
        } ${bouncing ? "animate-bounce-y" : ""}`}
        style={{ width: "240px", height: "240px", touchAction: "none" }}
      >
        <img
          src={Ball}
          alt="축구공"
          className="w-full h-full"
        />
      </button>

      {/* 파티클 */}
      {particles.map((p) => (
        <div
          key={p.id}
          className="particle"
          style={{
            left: p.x,
            top: p.y,
            "--dx": p.dx,
            "--dy": p.dy,
          }}
          onAnimationEnd={() => handleParticleEnd(p.id)}
        />
      ))}
    </div>
  );
}
