// src/components/StartScreen.js
import React, { useState } from 'react';
import Ball from '../assets/soccerball.png';
import Imoji from '../assets/imoji.png';

export default function StartScreen({ onStart }) {
  const [name, setName] = useState('');

  const handleClick = () => {
    if (!name.trim()) {
      alert('이름을 입력해주세요.');
      return;
    }

    // ✅ GA4 이벤트 전송
    if (typeof window.gtag === 'function') {
      window.gtag('event', 'game_start', {
        user_name: name.trim(),
      });
    }

    onStart(name.trim());
  };

  return (
    <div className="flex flex-col items-center justify-center h-full px-6 text-center space-y-4">
      {/* 상단 문구 */}
      <h2 className="text-white text-base font-medium">
        가장 빠른 사람은 누구?!
      </h2>

      {/* 제목 + 이모지 */}
      <div className="flex items-center justify-center gap-2">
        <h1 className="text-white text-3xl font-bold">
          축구공 연타 대결
        </h1>
        <img src={Imoji} alt="이모지" className="w-7 h-7" />
      </div>

      {/* 이름 입력 */}
      <input
        type="text"
        value={name}
        onChange={e => setName(e.target.value)}
        placeholder="이름을 입력하세요"
        className="w-full max-w-xs px-4 py-2 rounded-md border border-gray-300 text-black focus:outline-none text-center"
      />

      {/* 공 버튼 */}
      <button
        onClick={handleClick}
        className="cursor-pointer transform hover:scale-105 active:scale-95 transition-transform duration-200"
      >
        <img
          src={Ball}
          alt="공을 누르면 시작"
          className="w-36 h-36 mx-auto"
        />
      </button>

      {/* 안내 문구 */}
      <p className="text-white text-sm">공을 누르면 시작해요!</p>
    </div>
  );
}
