// src/components/TeamSelectScreen.js
import React, { useState, useEffect } from 'react';
import {
  addTeam,
  subscribeTeamsList,
  subscribeTeamRanking,
} from '../firebase';
import { useMode } from '../ModeContext';

export default function TeamSelectScreen({ onStartGame }) {
  const { setTeamId } = useMode();
  const [teams, setTeams] = useState([]);
  const [topTeam, setTopTeam] = useState('매치업 FC');
  const [myName, setMyName] = useState('');
  const [isModalOpen, setModalOpen] = useState(false);
  const [selectedId, setSelectedId] = useState(null); // ✅ 초기 선택 없음

  useEffect(() => {
    const unsubTeams = subscribeTeamsList((list) => setTeams(list));
    const unsubRank = subscribeTeamRanking(5, (rank) => {
      if (rank.length > 0) {
        const winner = teams.find((t) => t.id === rank[0].teamId);
        setTopTeam(winner ? winner.name : '—');
      }
    });
    return () => {
      unsubTeams();
      unsubRank();
    };
  }, [teams]);

  const handleAdd = () => {
    if (!myName.trim()) {
      alert('팀명을 입력해주세요');
      return;
    }
    addTeam(myName.trim()).then((id) => {
      setTeamId(id);
      setModalOpen(false);
      setMyName('');
    });
  };

  const handleStart = () => {
    if (!selectedId) {
      alert('팀을 선택해주세요');
      return;
    }
    setTeamId(selectedId);
    onStartGame();
  };

  return (
    <div className="relative w-full h-full flex flex-col items-center justify-start pt-24 pb-32 px-4 overflow-y-auto">
      {/* 타이틀 */}
      <div className="text-center text-white w-full mb-6">
        <h2 className="text-xl font-bold leading-tight mb-2">
          현재 경기장은<br />
          <span className="text-white font-black">{topTeam}</span> 가 점령중!
        </h2>
        <p className="text-sm mb-4">우리 팀을 선택해주세요</p>
      </div>

      {/* 팀 목록 */}
      <div className="flex flex-col gap-2 w-full">
        {teams.map((t) => (
          <button
            key={t.id}
            onClick={() => setSelectedId(t.id)}
            className={`w-full py-2 rounded text-sm font-semibold ${
              selectedId === t.id
                ? 'bg-[#C7FF00] text-black'
                : 'bg-white text-black'
            }`}
          >
            {t.name}
          </button>
        ))}
      </div>

      {/* 우리 팀 추가하기 버튼 */}
      <button
        onClick={() => setModalOpen(true)}
        className="mt-6 w-full py-2 border border-white text-white rounded text-sm"
      >
        우리 팀 추가하기
      </button>

      {/* 선택 완료 버튼 (색상 동적 변경) */}
      <div className="fixed bottom-4 left-1/2 -translate-x-1/2 w-[calc(100%-32px)] max-w-[375px] px-4 z-20">
        <button
          onClick={handleStart}
          className={`w-full py-3 rounded text-lg font-bold transition-all duration-200 ${
            selectedId
              ? 'bg-[#C7FF00] text-black'
              : 'bg-[#e9e9e9] text-gray-500 cursor-not-allowed'
          }`}
        >
          선택 완료
        </button>
      </div>

      {/* 팀 추가 모달 */}
      {isModalOpen && (
        <div className="fixed inset-0 z-40 bg-black bg-opacity-60 flex items-center justify-center">
          <div className="bg-white w-[85%] max-w-[320px] p-6 rounded-xl text-center">
            <h3 className="text-lg font-bold mb-4">우리 팀 추가하기</h3>
            <input
              className="w-full border-b border-[#C7FF00] text-black text-sm text-center py-2 outline-none mb-4"
              placeholder="팀명 입력..."
              value={myName}
              onChange={(e) => setMyName(e.target.value)}
            />
            <button
              onClick={handleAdd}
              className="w-full py-2 bg-[#C7FF00] text-black rounded font-bold text-sm"
            >
              등록하기
            </button>
          </div>
        </div>
      )}
    </div>
  );
}
