// src/firebase.js
import { initializeApp } from "firebase/app";
import {
  getDatabase,
  ref,
  push,
  onValue,
  query,
  orderByChild,
  limitToLast,
} from "firebase/database";

// 🔑 Firebase 설정
const firebaseConfig = {
  apiKey: process.env.REACT_APP_GOOGLE_API_KEY,
  authDomain: "hitgame-1e87c.firebaseapp.com",
  databaseURL: "https://hitgame-1e87c-default-rtdb.firebaseio.com",
  projectId: "hitgame-1e87c",
  storageBucket: "hitgame-1e87c.appspot.com",
  messagingSenderId: "939254271917",
  appId: "1:939254271917:web:80f5767b8cca88588d1b24",
  measurementId: "G-XPET2S0V5M",
};

const app = initializeApp(firebaseConfig);
export const db = getDatabase(app);

//
// 📌 점수 등록 관련
//

// 모드별 경로 지정
function getListRef({ mode, teamId, stadiumId }) {
  switch (mode) {
    case "team":
      if (!teamId) throw new Error("teamId가 필요합니다.");
      return ref(db, `leaderboards/teams/${teamId}`);
    case "stadium":
      if (!stadiumId) throw new Error("stadiumId가 필요합니다.");
      return ref(db, `leaderboards/stadiums/${stadiumId}`);
    default:
      return ref(db, "leaderboards/personal");
  }
}

// 점수 등록 (공통)
// - score를 Number(score)로 명시하여 반드시 숫자형으로 저장
export function submitScore({ mode, name, score, teamId, stadiumId }) {
  const listRef = getListRef({ mode, teamId, stadiumId });
  return push(listRef, {
    name: name || "",
    score: Number(score),
    timestamp: Date.now(),
  });
}

//
// 📊 랭킹 구독 관련
//

/**
 * 개인전 / 팀전 / 구장전 상위 랭킹 구독
 * - 실시간으로 score를 기준으로 정렬된 상위 n개를 받아옵니다.
 * - 항상 score를 Number로 파싱한 뒤 내림차순 정렬
 *
 * @param {object}   options
 * @param {"personal"|"team"|"stadium"} options.mode
 * @param {number}   options.limit       가져올 랭킹 개수 (기본 5)
 * @param {function} options.callback    [{ key, name, score, timestamp }, …] 배열 콜백
 * @param {string}   [options.teamId]
 * @param {string}   [options.stadiumId]
 * @returns {function}  // onValue 해제 함수
 */
export function subscribeTopScores({
  mode = "personal",
  limit = 5,
  callback,
  teamId = null,
  stadiumId = null,
}) {
  const n = Number(limit) > 0 ? Math.floor(limit) : 5;
  const listRef = getListRef({ mode, teamId, stadiumId });
  const q = query(listRef, orderByChild("score"), limitToLast(n));

  const unsub = onValue(q, (snapshot) => {
    const arr = [];
    snapshot.forEach((childSnap) => {
      const data = childSnap.val();
      arr.push({
        key: childSnap.key,
        name: data.name || "",
        score: Number(data.score),
        timestamp: data.timestamp,
      });
    });
    // 내림차순 정렬: 큰 점수부터 작은 점수 순으로
    arr.sort((a, b) => b.score - a.score);
    callback(arr);
  });

  return unsub;
}

//
// 🏷 팀 관련
//

// 전체 팀 저장 경로
const teamsRef = ref(db, "teams");

// 팀 추가 (개발자가 또는 팀 선택 화면에서 직접 호출)
export function addTeam(name) {
  return push(teamsRef, {
    name,
    timestamp: Date.now(),
  }).then((ch) => ch.key);
}

// 팀 목록 구독
export function subscribeTeamsList(callback) {
  const unsub = onValue(teamsRef, (snap) => {
    const list = [];
    snap.forEach((ch) => {
      list.push({ id: ch.key, name: ch.val().name });
    });
    callback(list);
  });
  return unsub;
}

// 팀별 최고점 랭킹
export function subscribeTeamRanking(limit, callback) {
  const lbRef = ref(db, "leaderboards/teams");
  const unsub = onValue(lbRef, (snap) => {
    const ranking = [];
    snap.forEach((teamSnap) => {
      let max = 0;
      teamSnap.forEach((entry) => {
        max = Math.max(max, Number(entry.val().score));
      });
      ranking.push({ teamId: teamSnap.key, score: max });
    });
    ranking.sort((a, b) => b.score - a.score);
    callback(ranking.slice(0, limit));
  });
  return unsub;
}

//
// 🏟 구장 관련
//

// 전체 구장 저장 경로 (개발자가 미리 이 경로에 구장 정보를 등록해야 합니다)
const stadiumsRef = ref(db, "stadiums");

// 구장 목록 구독 (개발자가 미리 등록해둔 데이터를 읽어올 때 사용)
export function subscribeStadiumsList(callback) {
  const unsub = onValue(stadiumsRef, (snap) => {
    const list = [];
    snap.forEach((ch) => {
      list.push({ id: ch.key, name: ch.val().name });
    });
    callback(list);
  });
  return unsub;
}

